    stx.prepend('updateChartData', function () {
        window.stx = stx;
        stx.prevQuote = this.currentQuote();
        stx.isNewTick = true;
        // if (stx.animations.liveScroll && stx.animations.liveScroll.running) {
        //     stx.animations.liveScroll.stop();
        // }
        // // this.micropixels = this.layout.candleWidth / 2;
        // this.draw();
    });

    Math.linearTween = function (t, b, c, d) {
        return c * t / d + b;
    };

    Math.easeOutExpo = function (t, b, c, d) {
        return c * (-Math.pow(2, -10 * t / d) + 1) + b; // eslint-disable-line
    };

    stx.append('updateChartData', function (newQuotes) {
        if (!stx.isSmoothScroll && newQuotes && newQuotes.length) {
            stx.animations.liveScroll = stx.animations.liveScroll || new CIQ.EaseMachine(Math.easeOutExpo, 3000000);
            // stx.isSmoothScroll = setInterval(() => {
            if (stx.isHome()/*  && stx.chart.dataSegment.length > 2  */&& newQuotes.slice(-1)[0].DT > this.prevQuote.DT) {
                if (!stx.animations.liveScroll.running /* && Math.abs(this.micropixels) <= this.layout.candleWidth */) {
                    stx.animations.liveScroll.run((x) => {
                        // if (Math.abs(this.micropixels) >= this.layout.candleWidth) {
                        //     console.log('scroll left', this.layout.candleWidth, this.chart.scroll, this.chart.lockScroll, this.micropixels);
                        //     this.micropixels = 0;
                        //     // this.chart.scroll -= 1;
                        // }
                        if (!stx.isHome()) this.animations.liveScroll.stop();

                        if (Math.abs(this.micropixels / this.layout.candleWidth) < 1) {
                            console.log('micro', this.micropixels, 'x', x);
                            let interval = 0;
                            try {
                                interval = Math.floor((this.chart.dataSegment.slice(-1)[0].DT.getTime() - this.chart.dataSegment.slice(-2)[0].DT.getTime()) / 1000);
                            } catch (err) {
                                interval = 2;
                            }
                            const temp = this.micropixels - (this.layout.candleWidth / (50 * interval));
                            this.micropixels = temp || temp === 0 ? temp : this.micropixels;
                            console.log('before', this.micropixels, this.chart.scroll);
                            this.draw();
                        } else {
                            // if (Math.abs(this.micropixels / this.layout.candleWidth) >= 1) {
                            const sign = this.micropixels < 0 ? -1 : 1;
                            // this.micropixels = (Math.abs(this.micropixels) % this.layout.candleWidth) * sign;
                            this.micropixels = (Math.abs(this.micropixels) - this.layout.candleWidth) * sign;

                            if (stx.isNewTick) {
                                this.chart.scroll = this.chart.dataSegment.length - 1;
                            } else {
                                this.chart.scroll--;
                            }

                            this.draw();
                            console.log('After new Tick', this.micropixels, this.chart.scroll);
                            // this.animations.liveScroll.stop();
                            // this.animations.liveScroll.reset();
                            this.animations.liveScroll.currentValues.default = 0;
                            this.animations.liveScroll.startTime = new Date().getTime();
                            console.log(this.micropixels, this.chart.scroll);
                        }
                        stx.isNewTick = false;
                    }, 0, this.layout.candleWidth);
                }/*  else if (Math.abs(this.micropixels) >= this.layout.candleWidth) { */
                // this.micropixels = 0;
                // this.chart.scroll = this.chart.dataSegment.length; // this.chart.maxTicks - Math.floor(this.chart.maxTicks / 5);
                // this.animations.liveScroll.stop();
                // this.draw();
                // console.log(this.micropixels);
                /* } */
            }
            // }, 1000);
        }
    });

